.dateContainer {
  display: inline-flex;
}

.dateText {
  font-size: 20px;
  font-weight: bold;
}
.dateText/*[scheme="client_light"]*/ {
  color: #9cf;
}

.monthText {
  padding-left: 4px;
  /*font-weight: 300;*/
  font-weight: normal;
}

.dayText {
  text-align: center;
  font-size: 16px;
  font-weight: bold;
}
.dayText/*[scheme="client_light"]*/ {
  color: #9cf;
}

.singleContainer {
  margin: 0px 3px;
  height: 50px;
  min-width: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  font-size: 12px;
  border-radius: 5px;
}
